<template>
  <VueMultiselect
    v-model="vmodel"
    :multiple="isMultiselect"
    :options="options"
    :searchable="true"
    :close-on-select="true"
    :allow-empty="false"
    :disabled="disabled"
    :placeholder="placeholder"
    :track-by="trackBy"
    :label="showBy"
    select-label=" "
    select-deselect-label=" "
    @select="onInputHandler"
  >
    <template #noResult>
      {{ 'Topilmadi' }}
    </template>
    <template #noOptions>
      {{ ' ' }}
    </template>
  </VueMultiselect>
</template>

<script></script>
<script setup>
import VueMultiselect from 'vue-multiselect';
import {defineProps, toRefs, ref, watch, defineEmits} from 'vue';
const props = defineProps({
  placeholder: {
    type: String,
    default: '',
    required: false,
  },
  trackBy: {
    type: String,
    required: true,
  },
  showBy: {
    type: String,
    required: true,
  },
  modelValue: {
    validator: function (value) {
      return ['object', 'string', 'number', 'null'].indexOf(typeof value) !== -1;
    },
    default: null,
    required: true,
  },
  isMultiselect: {
    type: Boolean,
    default: false,
    required: false,
  },
  options: {
    type: [Array],
    required: true,
  },
  disabled: {
    type: Boolean,
    default: false,
    required: false,
  },
});
const {placeholder, trackBy, showBy, options, isMultiselect, modelValue, disabled} = toRefs(props);
const emit = defineEmits(['update:modelValue', 'input']);

const vmodel = ref();

watch(
  () => modelValue.value,
  async () => {
    if (!vmodel.value && modelValue.value) {
      if (isMultiselect.value) {
        vmodel.value = options.value.filter((opt) => opt[trackBy.value] === modelValue.value);
      } else {
        vmodel.value = options.value.find((opt) => opt[trackBy.value] === modelValue.value);
      }
      // console.log(options.value);
    }
  },
  {
    immediate: true,
  }
);
watch(
  () => vmodel.value,
  () => {
    if (vmodel.value) {
      if (isMultiselect.value) {
        emit(
          'update:modelValue',
          vmodel.value.map((opt) => opt[trackBy.value])
        );
      } else {
        emit('update:modelValue', vmodel.value[trackBy.value]);
      }
    }
  },
  {
    immediate: true,
  }
);

const onInputHandler = (val) => {
  emit('input', {target: {value: val.id}});
};
</script>

<style src="vue-multiselect/dist/vue-multiselect.css"></style>
